import { useState, useCallback, useMemo, memo } from "react";
import { useNavigate } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import { useComments, useAddComment, useDeleteComment, Comment } from "@/hooks/useComments";
import { useAuth } from "@/contexts/AuthContext";
import { useProfile } from "@/hooks/useProfile";
import { useUserRole } from "@/hooks/useUserRole";
import { useTranslation } from "react-i18next";
import { useCheckSensitiveWords } from "@/hooks/useSensitiveWords";
import { formatDistanceToNow } from "date-fns";
import { MessageSquare, Trash2, Reply, Image as ImageIcon, X, Star, DollarSign, ShieldAlert, VolumeX, Clock, AlertCircle } from "lucide-react";
import { Badge } from "@/components/ui/badge";
import { supabase } from "@/integrations/supabase/client";
import { useToast } from "@/hooks/use-toast";
import { cn } from "@/lib/utils";
import ReportDialog from "@/components/ReportDialog";
import TranslatableText from "@/components/TranslatableText";
import ImagePreviewModal from "@/components/ImagePreviewModal";

interface RestaurantCommentsProps {
  restaurantId?: string;
  targetId?: string;
  targetType?: "restaurant" | "activity" | "post";
}

const priceRanges = [
  { value: "0-5", label: "0-5€" },
  { value: "5-10", label: "5-10€" },
  { value: "10-15", label: "10-15€" },
  { value: "15-20", label: "15-20€" },
  { value: "20-25", label: "20-25€" },
  { value: "25-30", label: "25-30€" },
  { value: "30-40", label: "30-40€" },
  { value: "40-50", label: "40-50€" },
  { value: "50+", label: "50€+" },
];

// Component for comment image with long image detection
const CommentImage = memo(({ imageUrl, onPreview }: { imageUrl: string; onPreview: () => void }) => {
  const { t } = useTranslation();
  const [isLongImage, setIsLongImage] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);

  const handleImageLoad = useCallback((e: React.SyntheticEvent<HTMLImageElement>) => {
    const img = e.currentTarget;
    const aspectRatio = img.naturalHeight / img.naturalWidth;
    setIsLongImage(aspectRatio > 1.5);
    setImageLoaded(true);
  }, []);

  return (
    <div className="relative mt-2 inline-block max-w-full sm:max-w-sm w-full">
      <img
        src={imageUrl}
        alt="Comment attachment"
        className="rounded-lg w-full max-h-[300px] sm:max-h-[400px] object-cover object-top cursor-pointer hover:opacity-90 transition-opacity"
        onClick={onPreview}
        onLoad={handleImageLoad}
      />
      {imageLoaded && isLongImage && (
        <Badge 
          variant="secondary" 
          className="absolute bottom-2 right-2 bg-black/60 text-white text-xs px-1.5 py-0.5"
        >
          {t("community.longImage", "长图")}
        </Badge>
      )}
    </div>
  );
});
CommentImage.displayName = "CommentImage";

const RestaurantComments = ({ restaurantId, targetId, targetType = "restaurant" }: RestaurantCommentsProps) => {
  // Support both old prop name (restaurantId) and new prop name (targetId)
  const actualTargetId = targetId || restaurantId || "";
  const { t } = useTranslation();
  const { user } = useAuth();
  const { data: profile } = useProfile();
  const { isAdmin } = useUserRole();
  const { toast } = useToast();
  const navigate = useNavigate();
  const [newComment, setNewComment] = useState("");
  const [rating, setRating] = useState(0);
  const [hoverRating, setHoverRating] = useState(0);
  const [priceRange, setPriceRange] = useState("");
  const [replyTo, setReplyTo] = useState<string | null>(null);
  const [replyContent, setReplyContent] = useState("");
  const [commentImage, setCommentImage] = useState<File | null>(null);
  const [commentImagePreview, setCommentImagePreview] = useState<string | null>(null);
  const [replyImage, setReplyImage] = useState<File | null>(null);
  const [replyImagePreview, setReplyImagePreview] = useState<string | null>(null);
  const [uploading, setUploading] = useState(false);
  const [showLoginDialog, setShowLoginDialog] = useState(false);
  const [deleteConfirmId, setDeleteConfirmId] = useState<string | null>(null);
  const [previewImageUrl, setPreviewImageUrl] = useState<string | null>(null);

  const { data: comments, isLoading } = useComments(actualTargetId, targetType);
  const addComment = useAddComment();
  const deleteComment = useDeleteComment();
  const { checkContent } = useCheckSensitiveWords();

  const uploadImage = useCallback(async (file: File): Promise<string | null> => {
    try {
      const fileExt = file.name.split(".").pop();
      const fileName = `${user?.id}-${Date.now()}.${fileExt}`;
      const filePath = `${fileName}`;

      const { error: uploadError, data } = await supabase.storage
        .from("comment-images")
        .upload(filePath, file);

      if (uploadError) throw uploadError;

      const { data: { publicUrl } } = supabase.storage
        .from("comment-images")
        .getPublicUrl(filePath);

      return publicUrl;
    } catch (error) {
      console.error("Error uploading image:", error);
      toast({
        title: t("common.error"),
        description: t("restaurant.imageUploadFailed"),
        variant: "destructive",
      });
      return null;
    }
  }, [user, toast, t]);

  const handleImageSelect = useCallback((e: React.ChangeEvent<HTMLInputElement>, isReply: boolean = false) => {
    const file = e.target.files?.[0];
    if (file) {
      if (file.size > 5 * 1024 * 1024) {
        toast({
          title: t("common.error"),
          description: t("restaurant.imageTooLarge"),
          variant: "destructive",
        });
        return;
      }

      if (isReply) {
        setReplyImage(file);
        setReplyImagePreview(URL.createObjectURL(file));
      } else {
        setCommentImage(file);
        setCommentImagePreview(URL.createObjectURL(file));
      }
    }
  }, [toast, t]);

  const clearImage = useCallback((isReply: boolean = false) => {
    if (isReply) {
      setReplyImage(null);
      setReplyImagePreview(null);
    } else {
      setCommentImage(null);
      setCommentImagePreview(null);
    }
  }, []);

  const handleSubmitComment = useCallback(async () => {
    if (!newComment.trim() || !user) return;
    
    // Check if user is muted
    if (profile?.is_muted) {
      toast({
        title: t("common.error"),
        description: t("restaurant.youAreMuted"),
        variant: "destructive",
      });
      return;
    }
    
    // Check for sensitive words
    const sensitiveCheck = checkContent(newComment);
    if (sensitiveCheck.hasSensitive) {
      toast({
        title: t("common.error"),
        description: t("restaurant.sensitiveWordsDetected", "评论包含敏感词，请修改后重试"),
        variant: "destructive",
      });
      return;
    }
    
    // For restaurant comments, require rating
    if (targetType === "restaurant" && !rating) {
      toast({
        title: t("common.hint", "提示"),
        description: t("toast.pleaseSelectRating"),
        variant: "warning",
      });
      return;
    }

    setUploading(true);
    let imageUrl: string | undefined;

    if (commentImage) {
      const uploadedUrl = await uploadImage(commentImage);
      if (uploadedUrl) imageUrl = uploadedUrl;
    }

    await addComment.mutateAsync({
      targetId: actualTargetId,
      targetType: targetType,
      content: newComment,
      ratingValue: targetType === "restaurant" ? rating : undefined,
      priceRange: targetType === "restaurant" && priceRange ? priceRange : undefined,
      imageUrl,
    });

    setNewComment("");
    setRating(0);
    setPriceRange("");
    setCommentImage(null);
    setCommentImagePreview(null);
    setUploading(false);
  }, [newComment, user, profile, targetType, rating, toast, t, commentImage, uploadImage, addComment, restaurantId, priceRange, checkContent]);

  const handleSubmitReply = useCallback(async (parentId: string) => {
    if (!replyContent.trim() || !user) return;

    // Check if user is muted
    if (profile?.is_muted) {
      toast({
        title: t("common.error"),
        description: t("restaurant.youAreMuted"),
        variant: "destructive",
      });
      return;
    }

    // Check for sensitive words
    const sensitiveCheck = checkContent(replyContent);
    if (sensitiveCheck.hasSensitive) {
      toast({
        title: t("common.error"),
        description: t("restaurant.sensitiveWordsDetected", "评论包含敏感词，请修改后重试"),
        variant: "destructive",
      });
      return;
    }

    setUploading(true);
    let imageUrl: string | undefined;

    if (replyImage) {
      const uploadedUrl = await uploadImage(replyImage);
      if (uploadedUrl) imageUrl = uploadedUrl;
    }

    await addComment.mutateAsync({
      targetId: actualTargetId,
      targetType: targetType,
      content: replyContent,
      parentId,
      imageUrl,
    });

    setReplyContent("");
    setReplyTo(null);
    setReplyImage(null);
    setReplyImagePreview(null);
    setUploading(false);
  }, [replyContent, user, profile, replyImage, uploadImage, addComment, restaurantId, targetType, checkContent, toast, t]);

  const handleDeleteComment = useCallback(async (commentId: string) => {
    await deleteComment.mutateAsync(commentId);
    setDeleteConfirmId(null);
  }, [deleteComment]);

  const CommentItem = memo(({ comment, isReply = false }: { comment: Comment; isReply?: boolean }) => (
    <div className={`flex gap-2 sm:gap-3 ${isReply ? "ml-8 sm:ml-12 mt-3" : ""}`}>
      <Avatar className="w-8 h-8 sm:w-10 sm:h-10 shrink-0">
        <AvatarImage src={comment.profiles?.avatar_url} />
        <AvatarFallback className="text-xs sm:text-sm">
          {comment.profiles?.display_name?.charAt(0).toUpperCase() || "U"}
        </AvatarFallback>
      </Avatar>
      <div className="flex-1 min-w-0">
        <div className="flex items-center gap-2 flex-wrap">
          <span className="font-semibold text-xs sm:text-sm truncate">{comment.profiles?.display_name}</span>
          <span className="text-xs text-muted-foreground shrink-0">
            {formatDistanceToNow(new Date(comment.created_at), { addSuffix: true })}
          </span>
          {comment.edited && (
            <span className="text-xs text-muted-foreground italic">({t("restaurant.edited")})</span>
          )}
          {/* Show moderation status badges for user's own comments */}
          {user?.id === comment.user_id && comment.moderation_status === "pending" && (
            <Badge variant="outline" className="text-yellow-600 border-yellow-500 bg-yellow-50 dark:bg-yellow-950/30 text-xs py-0 h-5">
              <Clock className="w-3 h-3 mr-1" />
              {t("restaurant.commentPending", "审核中")}
            </Badge>
          )}
          {user?.id === comment.user_id && comment.moderation_status === "flagged" && (
            <Badge variant="outline" className="text-destructive border-destructive bg-destructive/10 text-xs py-0 h-5">
              <AlertCircle className="w-3 h-3 mr-1" />
              {t("restaurant.commentFailed", "发送失败")}
            </Badge>
          )}
        </div>
        
        {/* Display rating and price range for main comments */}
        {!isReply && comment.rating_value && (
          <div className="flex items-center gap-3 mt-1 mb-2">
            <div className="flex items-center gap-1">
              {[1, 2, 3, 4, 5].map((star) => (
                <Star
                  key={star}
                  className={cn(
                    "w-4 h-4",
                    star <= comment.rating_value!
                      ? "fill-accent text-accent"
                      : "text-muted-foreground"
                  )}
                />
              ))}
            </div>
            {comment.price_range && (
              <div className="flex items-center gap-1 text-xs text-muted-foreground">
                <DollarSign className="w-3 h-3" />
                <span>{priceRanges.find(r => r.value === comment.price_range)?.label}</span>
              </div>
            )}
          </div>
        )}
        
        <TranslatableText text={comment.content} className="mt-1" />
        {comment.image_url && (
          <CommentImage 
            imageUrl={comment.image_url} 
            onPreview={() => setPreviewImageUrl(comment.image_url!)} 
          />
        )}
        <div className="flex gap-2 sm:gap-3 mt-2">
          {!isReply && user && !profile?.is_muted && (
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setReplyTo(comment.id)}
              className="h-7 text-xs px-2 sm:px-3"
            >
              <Reply className="w-3 h-3 mr-1" />
              <span className="hidden xs:inline">{t("restaurant.reply")}</span>
            </Button>
          )}
          {(user?.id === comment.user_id || isAdmin) && (
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setDeleteConfirmId(comment.id)}
              className="h-7 text-xs text-destructive hover:text-destructive px-2 sm:px-3"
            >
              <Trash2 className="w-3 h-3 mr-1" />
              <span className="hidden xs:inline">{t("common.delete")}</span>
            </Button>
          )}
          {user?.id !== comment.user_id && (
            <ReportDialog
              targetType="comment"
              targetId={comment.id}
              onLoginRequired={() => setShowLoginDialog(true)}
              trigger={
                <Button
                  variant="ghost"
                  size="sm"
                  className="h-7 text-xs px-2 sm:px-3"
                >
                  <ShieldAlert className="w-3 h-3 mr-1" />
                  <span className="hidden xs:inline">{t("report.title")}</span>
                </Button>
              }
            />
          )}
        </div>

        {replyTo === comment.id && (
          <div className="mt-3 space-y-2">
            <div className="flex flex-col sm:flex-row gap-2">
              <div className="flex-1 space-y-2">
                <Textarea
                  value={replyContent}
                  onChange={(e) => setReplyContent(e.target.value)}
                  placeholder={t("restaurant.writeReply")}
                  className="min-h-[80px] text-sm"
                />
                  {replyImagePreview && (
                  <div className="relative inline-block">
                    <img
                      src={replyImagePreview}
                      alt="Preview"
                      className="w-24 h-24 object-cover rounded-lg cursor-pointer hover:opacity-90 transition-opacity"
                      onClick={() => setPreviewImageUrl(replyImagePreview)}
                    />
                    <Button
                      size="icon"
                      variant="destructive"
                      className="absolute -top-2 -right-2 h-6 w-6 rounded-full"
                      onClick={() => clearImage(true)}
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                )}
              </div>
              <div className="flex sm:flex-col gap-2">
                <Button
                  size="sm"
                  onClick={() => handleSubmitReply(comment.id)}
                  disabled={!replyContent.trim() || uploading}
                  className="text-xs"
                >
                  {uploading ? t("common.loading") : t("restaurant.reply")}
                </Button>
                <Input
                  type="file"
                  accept="image/*"
                  onChange={(e) => handleImageSelect(e, true)}
                  className="hidden"
                  id={`reply-image-${comment.id}`}
                  disabled={uploading}
                />
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => document.getElementById(`reply-image-${comment.id}`)?.click()}
                  disabled={uploading}
                  className="text-xs sm:px-3"
                >
                  <ImageIcon className="h-4 w-4 sm:mr-1" />
                  <span className="hidden sm:inline">{t("restaurant.addImage")}</span>
                </Button>
                <Button
                  size="sm"
                  variant="outline"
                  onClick={() => {
                    setReplyTo(null);
                    setReplyContent("");
                    clearImage(true);
                  }}
                  className="text-xs"
                >
                  {t("common.cancel")}
                </Button>
              </div>
            </div>
          </div>
        )}

        {comment.replies && comment.replies.length > 0 && (
          <div className="mt-3 space-y-3">
            {comment.replies.map((reply) => (
              <CommentItem key={reply.id} comment={reply} isReply />
            ))}
          </div>
        )}
      </div>
    </div>
  ));
  CommentItem.displayName = "CommentItem";

  if (isLoading) {
    return <div className="text-center py-8">{t("common.loading")}</div>;
  }

  return (
    <>
    <div className="space-y-4 sm:space-y-6">
      <div className="flex items-center gap-2">
        <MessageSquare className="w-4 sm:w-5 h-4 sm:h-5" />
        <h2 className="text-xl sm:text-2xl font-bold">
          {t("restaurant.reviews")} ({comments?.length || 0})
        </h2>
      </div>

      {user ? (
        profile?.is_muted ? (
          <Card className="border-yellow-300 bg-yellow-50/50 dark:bg-yellow-950/20">
            <CardContent className="py-4 sm:py-6 px-3 sm:px-6">
              <div className="flex items-center gap-3 text-yellow-600 dark:text-yellow-400">
                <VolumeX className="w-5 h-5 shrink-0" />
                <span className="text-sm font-medium">{t("restaurant.youAreMutedNotice")}</span>
              </div>
            </CardContent>
          </Card>
        ) : (
        <Card>
          <CardContent className="pt-4 sm:pt-6 px-3 sm:px-6">
            <div className="flex gap-2 sm:gap-3">
              <Avatar className="w-8 h-8 sm:w-10 sm:h-10 shrink-0">
                <AvatarImage src={profile?.avatar_url || undefined} />
                <AvatarFallback className="text-xs sm:text-sm">
                  {profile?.display_name?.charAt(0).toUpperCase() || user?.email?.charAt(0).toUpperCase() || "U"}
                </AvatarFallback>
              </Avatar>
              <div className="flex-1 space-y-2 sm:space-y-3 min-w-0">
                {/* Rating and Price Range - Only for restaurant */}
                {targetType === "restaurant" && (
                  <div className="space-y-3">
                    <div className="space-y-2">
                      <label className="text-sm font-medium flex items-center gap-2">
                        <Star className="w-4 h-4 text-accent" />
                        评分 <span className="text-destructive">*</span>
                      </label>
                      <div className="flex items-center gap-2">
                        {[1, 2, 3, 4, 5].map((star) => (
                          <button
                            key={star}
                            type="button"
                            onMouseEnter={() => setHoverRating(star)}
                            onMouseLeave={() => setHoverRating(0)}
                            onClick={() => setRating(star)}
                            className="transition-transform hover:scale-110"
                          >
                            <Star
                              className={cn(
                                "w-7 h-7 cursor-pointer transition-colors",
                                star <= (hoverRating || rating)
                                  ? "fill-accent text-accent"
                                  : "text-muted-foreground hover:text-accent"
                              )}
                            />
                          </button>
                        ))}
                        {rating > 0 && (
                          <span className="ml-2 text-sm text-muted-foreground">
                            ({rating}/5)
                          </span>
                        )}
                      </div>
                    </div>

                    <div className="space-y-2">
                      <label className="text-sm font-medium flex items-center gap-2">
                        <DollarSign className="w-4 h-4" />
                        {t("restaurant.priceRange")}
                      </label>
                      <Select value={priceRange} onValueChange={setPriceRange}>
                        <SelectTrigger className="w-full">
                          <SelectValue placeholder={t("restaurant.selectPriceRange")} />
                        </SelectTrigger>
                        <SelectContent className="bg-background">
                          {priceRanges.map((range) => (
                            <SelectItem key={range.value} value={range.value}>
                              {range.label}
                            </SelectItem>
                          ))}
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                )}

                <Textarea
                  value={newComment}
                  onChange={(e) => setNewComment(e.target.value)}
                  placeholder={t("restaurant.writeComment")}
                  className="min-h-[80px] sm:min-h-[100px] text-sm"
                />
                {commentImagePreview && (
                  <div className="relative inline-block">
                    <img
                      src={commentImagePreview}
                      alt="Preview"
                      className="w-24 h-24 object-cover rounded-lg cursor-pointer hover:opacity-90 transition-opacity"
                      onClick={() => setPreviewImageUrl(commentImagePreview)}
                    />
                    <Button
                      size="icon"
                      variant="destructive"
                      className="absolute -top-2 -right-2 h-6 w-6 rounded-full"
                      onClick={() => clearImage(false)}
                    >
                      <X className="h-4 w-4" />
                    </Button>
                  </div>
                )}
                <div className="flex flex-wrap gap-2">
                  <Input
                    type="file"
                    accept="image/*"
                    onChange={(e) => handleImageSelect(e, false)}
                    className="hidden"
                    id="comment-image"
                    disabled={uploading}
                  />
                  <Button
                    variant="outline"
                    size="sm"
                    onClick={() => document.getElementById("comment-image")?.click()}
                    disabled={uploading}
                    className="text-xs"
                  >
                    <ImageIcon className="h-4 w-4 sm:mr-2" />
                    <span className="hidden sm:inline">{t("restaurant.addImage")}</span>
                  </Button>
                  <Button
                    onClick={handleSubmitComment}
                    disabled={!newComment.trim() || addComment.isPending || uploading}
                    size="sm"
                    className="text-xs"
                  >
                    {uploading || addComment.isPending ? t("common.loading") : t("restaurant.postComment")}
                  </Button>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
        )
      ) : (
        <Card>
          <CardContent className="py-4 sm:py-6 px-3 sm:px-6 text-center text-muted-foreground text-sm">
            {t("restaurant.signInToComment")}
          </CardContent>
        </Card>
      )}

      <div className="space-y-4 sm:space-y-6">
        {comments?.map((comment) => (
          <Card key={comment.id}>
            <CardHeader className="p-3 sm:p-6">
              <CommentItem comment={comment} />
            </CardHeader>
          </Card>
        ))}
      </div>

      {comments?.length === 0 && (
        <Card>
          <CardContent className="py-8 sm:py-12 px-3 sm:px-6 text-center text-muted-foreground text-sm">
            {t("restaurant.noComments")}
          </CardContent>
        </Card>
      )}
    </div>

    {/* Login Required Dialog */}
    <AlertDialog open={showLoginDialog} onOpenChange={setShowLoginDialog}>
      <AlertDialogContent>
        <AlertDialogHeader>
          <AlertDialogTitle>{t("activities.loginRequired")}</AlertDialogTitle>
          <AlertDialogDescription>
            {t("activities.loginRequiredMessage")}
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter>
          <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
          <AlertDialogAction onClick={() => navigate("/auth")}>
            {t("activities.goToLogin")}
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>

    {/* Delete Comment Confirmation Dialog */}
    <AlertDialog open={!!deleteConfirmId} onOpenChange={(open) => !open && setDeleteConfirmId(null)}>
      <AlertDialogContent className="max-w-sm">
        <AlertDialogHeader>
          <div className="mx-auto mb-4 flex h-14 w-14 items-center justify-center rounded-full bg-destructive/10">
            <Trash2 className="h-7 w-7 text-destructive" />
          </div>
          <AlertDialogTitle className="text-center">
            {t("restaurant.deleteComment")}
          </AlertDialogTitle>
          <AlertDialogDescription className="text-center">
            {t("restaurant.confirmDelete")}
          </AlertDialogDescription>
        </AlertDialogHeader>
        <AlertDialogFooter className="sm:justify-center gap-2">
          <AlertDialogCancel className="sm:w-24">
            {t("common.cancel")}
          </AlertDialogCancel>
          <AlertDialogAction
            onClick={() => deleteConfirmId && handleDeleteComment(deleteConfirmId)}
            className="bg-destructive text-destructive-foreground hover:bg-destructive/90 sm:w-24"
          >
            {t("common.delete")}
          </AlertDialogAction>
        </AlertDialogFooter>
      </AlertDialogContent>
    </AlertDialog>
    
    {/* Image Preview Modal */}
    <ImagePreviewModal
      imageUrl={previewImageUrl || ""}
      isOpen={!!previewImageUrl}
      onClose={() => setPreviewImageUrl(null)}
    />
    </>
  );
};

export default RestaurantComments;
