import { useState } from "react";
import { useTranslation } from "react-i18next";
import { Languages, Loader2, RotateCcw } from "lucide-react";
import { Button } from "@/components/ui/button";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "sonner";

interface TranslateButtonProps {
  text: string;
  className?: string;
  onTranslation?: (translatedText: string | null) => void;
}

// Simple cache to store translations and avoid repeated API calls
const translationCache = new Map<string, string>();

const TranslateButton = ({ text, className, onTranslation }: TranslateButtonProps) => {
  const { t, i18n } = useTranslation();
  const [isTranslating, setIsTranslating] = useState(false);
  const [translatedText, setTranslatedText] = useState<string | null>(null);
  const [showTranslation, setShowTranslation] = useState(false);
  const [lastTranslatedLang, setLastTranslatedLang] = useState<string | null>(null);

  // Reset translation when language changes
  const currentLang = i18n.language;
  
  // If language changed and we have a translation, reset it
  if (lastTranslatedLang && lastTranslatedLang !== currentLang && showTranslation) {
    setShowTranslation(false);
    setTranslatedText(null);
    onTranslation?.(null);
  }

  const handleTranslate = async () => {
    // Get current language at call time, not render time
    const targetLang = i18n.language;
    const cacheKey = `${text.substring(0, 100)}_${targetLang}`;
    
    // If already showing translation for the same language, toggle back to original
    if (showTranslation && lastTranslatedLang === targetLang) {
      setShowTranslation(false);
      onTranslation?.(null);
      return;
    }

    // Check global cache
    const cached = translationCache.get(cacheKey);
    if (cached) {
      setTranslatedText(cached);
      setShowTranslation(true);
      setLastTranslatedLang(targetLang);
      onTranslation?.(cached);
      return;
    }

    // Call translation API
    setIsTranslating(true);
    console.log('[TranslateButton] Calling translate-content with targetLang:', targetLang);
    try {
      const { data, error } = await supabase.functions.invoke('translate-content', {
        body: { text, targetLang }
      });
      console.log('[TranslateButton] Response:', data, error);

      if (error) {
        console.error('Translation error:', error);
        toast.error(t("translate.error", "翻译失败，请稍后重试"));
        return;
      }

      if (data?.translatedText) {
        // Store in cache
        translationCache.set(cacheKey, data.translatedText);
        setTranslatedText(data.translatedText);
        setShowTranslation(true);
        setLastTranslatedLang(targetLang);
        onTranslation?.(data.translatedText);
      } else if (data?.error) {
        toast.error(data.error);
      }
    } catch (err) {
      console.error('Translation error:', err);
      toast.error(t("translate.error", "翻译失败，请稍后重试"));
    } finally {
      setIsTranslating(false);
    }
  };

  return (
    <Button
      variant="ghost"
      size="sm"
      className={`gap-1.5 h-auto py-1 px-2 text-xs text-muted-foreground hover:text-foreground ${className || ''}`}
      onClick={handleTranslate}
      disabled={isTranslating}
    >
      {isTranslating ? (
        <>
          <Loader2 className="h-3 w-3 animate-spin" />
          <span>{t("translate.translating", "翻译中...")}</span>
        </>
      ) : showTranslation ? (
        <>
          <RotateCcw className="h-3 w-3" />
          <span>{t("translate.showOriginal", "显示原文")}</span>
        </>
      ) : (
        <>
          <Languages className="h-3 w-3" />
          <span>{t("translate.button", "翻译")}</span>
        </>
      )}
    </Button>
  );
};

export default TranslateButton;
