import { useState, useEffect, useRef } from "react";
import { createPortal } from "react-dom";
import { X } from "lucide-react";
import { Button } from "@/components/ui/button";

interface ImagePreviewModalProps {
  imageUrl: string;
  isOpen: boolean;
  onClose: () => void;
}

const ImagePreviewModal = ({ imageUrl, isOpen, onClose }: ImagePreviewModalProps) => {
  const [isLongImage, setIsLongImage] = useState(false);
  const [imageLoaded, setImageLoaded] = useState(false);
  const [scrollStart, setScrollStart] = useState<{ y: number; scrollTop: number } | null>(null);
  const containerRef = useRef<HTMLDivElement>(null);

  // Lock body scroll when preview is open
  useEffect(() => {
    if (isOpen) {
      const scrollY = window.scrollY;
      document.body.style.position = 'fixed';
      document.body.style.top = `-${scrollY}px`;
      document.body.style.left = '0';
      document.body.style.right = '0';
      document.body.style.overflow = 'hidden';
      
      return () => {
        document.body.style.position = '';
        document.body.style.top = '';
        document.body.style.left = '';
        document.body.style.right = '';
        document.body.style.overflow = '';
        window.scrollTo(0, scrollY);
      };
    }
  }, [isOpen]);

  // Reset state when image URL changes
  useEffect(() => {
    setIsLongImage(false);
    setImageLoaded(false);
  }, [imageUrl]);

  const handleImageLoad = (e: React.SyntheticEvent<HTMLImageElement>) => {
    const img = e.currentTarget;
    const ratio = img.naturalHeight / img.naturalWidth;
    setIsLongImage(ratio > 1.5);
    setImageLoaded(true);
  };

  const handleContainerClick = (e: React.MouseEvent) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  const handleTouchEnd = (e: React.TouchEvent) => {
    if (!scrollStart) return;
    
    const touch = e.changedTouches[0];
    const deltaY = Math.abs(touch.clientY - scrollStart.y);
    const container = containerRef.current;
    const deltaScroll = container ? Math.abs(container.scrollTop - scrollStart.scrollTop) : 0;
    
    if (deltaY < 15 && deltaScroll < 15) {
      e.preventDefault();
      onClose();
    }
    setScrollStart(null);
  };

  if (!isOpen) return null;

  return createPortal(
    <div 
      className="fixed inset-0 z-[100] bg-black"
      style={{ 
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        width: '100vw',
        height: '100dvh',
        overflow: 'hidden',
        margin: 0,
        padding: 0
      }}
    >
      {/* Close Button */}
      <Button
        variant="ghost"
        size="icon"
        className="absolute top-4 right-4 z-10 text-white hover:bg-white/20"
        onClick={onClose}
      >
        <X className="h-6 w-6" />
      </Button>

      {/* Image Container */}
      <div 
        ref={containerRef}
        className="w-full h-full overflow-x-hidden overscroll-contain"
        style={{
          overflowY: isLongImage ? 'scroll' : 'hidden',
          WebkitOverflowScrolling: 'touch',
        }}
        onClick={handleContainerClick}
        onTouchStart={(e) => {
          setScrollStart({ 
            y: e.touches[0].clientY, 
            scrollTop: containerRef.current?.scrollTop || 0 
          });
        }}
        onTouchEnd={handleTouchEnd}
        onMouseDown={(e) => {
          setScrollStart({ 
            y: e.clientY, 
            scrollTop: containerRef.current?.scrollTop || 0 
          });
        }}
        onMouseUp={(e) => {
          if (scrollStart) {
            const deltaY = Math.abs(e.clientY - scrollStart.y);
            const container = containerRef.current;
            const deltaScroll = container ? Math.abs(container.scrollTop - scrollStart.scrollTop) : 0;
            if (deltaY < 10 && deltaScroll < 10) {
              onClose();
            }
            setScrollStart(null);
          }
        }}
      >
        {/* Hidden image to detect dimensions */}
        {!imageLoaded && (
          <img 
            src={imageUrl} 
            alt="" 
            onLoad={handleImageLoad}
            style={{ position: 'absolute', opacity: 0, pointerEvents: 'none' }}
          />
        )}
        
        {/* Actual visible image */}
        {imageLoaded && (
          isLongImage ? (
            <img
              src={imageUrl}
              alt=""
              draggable={false}
              className="block select-none"
              style={{ 
                width: '100vw',
                minWidth: '100vw',
                minHeight: '100dvh',
                height: 'auto',
                margin: 0,
                objectFit: 'cover',
                objectPosition: 'top center'
              }}
            />
          ) : (
            <div 
              className="w-full h-full flex items-center justify-center"
              onClick={onClose}
            >
              <img
                src={imageUrl}
                alt=""
                draggable={false}
                className="max-w-full max-h-full object-contain select-none"
              />
            </div>
          )
        )}
      </div>
    </div>,
    document.body
  );
};

export default ImagePreviewModal;
